import React, { useState } from 'react';
import axios from 'axios';
import csrftoken from '../csrf';

const IssueForm = () => {
    const [vehicleId, setVehicleId] = useState('');
    const [description, setDescription] = useState('');
    const [componentId, setComponentId] = useState('');
    const [repairNeeded, setRepairNeeded] = useState(false);

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            await axios.post('/api/issues/',
                {
                    vehicle: vehicleId,
                    description,
                    component: componentId,
                    repair_needed: repairNeeded
                },
                {
                    headers: {
                        'X-CSRFToken': csrftoken // Send the CSRF token
                    }
                }
            );
            // Reset form fields
            setVehicleId('');
            setDescription('');
            setComponentId('');
            setRepairNeeded(false);
        } catch (error) {
            console.error("There was an error!", error);
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <h2>Add Issue</h2>
            <div>
                <label>Vehicle ID:</label>
                <input type="text" value={vehicleId} onChange={(e) => setVehicleId(e.target.value)} required />
            </div>
            <div>
                <label>Description:</label>
                <input type="text" value={description} onChange={(e) => setDescription(e.target.value)} required />
            </div>
            <div>
                <label>Component ID:</label>
                <input type="text" value={componentId} onChange={(e) => setComponentId(e.target.value)} required />
            </div>
            <div>
                <label>Repair Needed:</label>
                <input type="checkbox" checked={repairNeeded} onChange={(e) => setRepairNeeded(e.target.checked)} />
            </div>
            <button type="submit">Submit</button>
        </form>
    );
};

export default IssueForm;
